.container {
    background-color: #0a0a0a;
    min-height: 100vh;
}

.expertSliders {
    max-height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 6rem;
}

.expertSliders h1 {
    margin: 0rem 0rem 0.5rem 0rem;
    align-self: baseline;
    margin-left: 8%;
    color: rgb(255, 255, 255);
    text-decoration: none;
    font-family: MontserratExtraBoldItalic;
    font-size: 36px;
}

.images {
    width: 85%;
    display: flex;
    flex-direction: row;
    overflow: auto;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 85%;
}

.button {
    color: white;
    background-color: rgb(0, 174, 239);
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 0.3rem;
    font-size: 22px;
    font-weight: bold;
    font-style: italic;
    text-transform: uppercase;
    position: relative;
}

.price {
    color: rgb(0, 215, 255);
    text-decoration: none;
    text-transform: uppercase;
    position: relative;
    margin: 0.4rem;
    font-family: MontserratMedium;
    font-size: 21px;
}

.copyright {
    color: white;
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 10px;
}

.copyright a {
    text-decoration: none;
    color: white;
    font-family: MontserratLight;
    font-size: 14px;
}

.copyright a:hover {
    color: rgb(0, 215, 255);
}

.copyright p {
    margin: 4px;
    font-family: MontserratLight;
    font-size: 14px;
}

@media only screen and (max-width: 600px) {
    .button {
        font-size: 16px;
    }
    .price {
        font-size: 14px;
        margin: 0.2rem;
    }
    .footer {
        position: relative;
        margin-top: 0;
        align-items: flex-end;
        justify-content: flex-end;
    }
    .copyright {
        display: none;
    }
    .expertSliders h1 {
        font-size: 32px;
    }
}

/* ------------Scroll Bar------------- */

::-webkit-scrollbar {
    height: 3px;
    width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
    border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(16 16 16);
    border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(16 16 16);
}

